.drawer {
  position: absolute;
  :global {
    .ant-drawer-body {
      padding: 0;
    }
    .ant-drawer-content-wrapper {
      padding-bottom: 1rem;
    }
    .ant-drawer-body::-webkit-scrollbar {
      display: none;
    }
  }
}

.text {
  :global {
    .ant-input {
      &:hover {
        cursor: default;
      }
      text-align: center;
      background-color: white;
      // border: 0;
      color: black;
      max-width: 285px;
    }
    .ant-input-disabled {
      background-color: white;
      // border: 0;
    }
  }
}
