.text {
  margin-top: 1rem;
  height: 140px;
  display: flex;
  justify-content: center;
  align-items: center;
  :global {
    .ant-input {
      top: -10px;
      text-align: center;
      background-color: white;
      border: 0;
    }
    .ant-input-disabled {
      top: -10px;
      background-color: white;
      border: 0;
      cursor: default;
    }
  }
}

.card {
  :global {
    .ant-card-body {
      height: 0;
      padding: 0;
    }
  }
}
