/* Change Autocomplete styles in Chrome*/
.cms-login input:-webkit-autofill,
.cms-login input:-webkit-autofill:hover, 
.cms-login input:-webkit-autofill:focus,
.cms-login textarea:-webkit-autofill,
.cms-login textarea:-webkit-autofill:hover,
.cms-login textarea:-webkit-autofill:focus,
.cms-login select:-webkit-autofill,
.cms-login select:-webkit-autofill:hover,
.cms-login select:-webkit-autofill:focus {
  
  -webkit-text-fill-color: white;
  /* -webkit-box-shadow: 0 0 0px 1000px #ffffff22 inset; */
  -webkit-transition: background-color 9999s ease-out;
  font-size: 1.5em !important;
}

.MuiButton-label {
  cursor: pointer;
}


.loginInput:focus, .loginInput:hover  {
  border:3px solid white !important;

}